import React, { Component } from 'react';

class DiscoverPiece extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        console.log(this.props.piece)
        let { piece } = this.props;
        let pieceImg = piece.imgUrls[Object.keys(piece.imgUrls)[0]]
        return (
            <div className="row discoverPiece">
                <div className="daPiece col-lg-4 col-lg-offset-4 col-md-6 col-md-offset-3 col-sm-10 col-sm-offset-1">
                    <img src={pieceImg} alt=""/>

                    <div className="row">
                        <div className="col-sm-6 colLeft">
                            <p className="small">Piece Name</p>
                            <p className="big">{piece.pieceName}</p>
                        </div>
                        <div className="col-sm-6 colRight">
                            <p className="small">Artist Username</p>
                            <p className="big">{piece.artistUsername}</p>
                        </div>
                    </div>
                    <p className="price">${piece.price}</p>
                </div>
            </div>
        )
    }
}

export default DiscoverPiece;


