import React, { Component } from 'react';
import { Link } from 'react-router';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        return (
            <nav className="navbar-fixed-to daNav navbar">
                <div className="container">
                    <div className="navbar-header">
                        <span className="navbar-brand">
                            <Link className="removeTextDeco" to={'/'}><p className="newLogo">muralist</p></Link>
                        </span>
                    </div>

                    <ul className="nav navbar-nav navbar-right">
                        <hr className="mobileHr"/>
                        {/* <li><Link to={'/story'}>The Story</Link></li> */}
                        <li><Link to={'/artists'}>The Artists</Link></li>
                        <li><Link to={'/discover'}>Discover</Link></li>
                        {/* <li><Link to={'/artists'}>Signup/Login</Link></li> */}
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Home;


